import { MutationKeys } from '@pi-xcels/graphql/constants';
import { useIsMutating } from '@tanstack/vue-query';
import { computed } from 'vue';

export const useIsMutationLoading = (mutationKey: MutationKeys[]) => {
  const mutationCount = useIsMutating({
    mutationKey,
  });
  return computed(() => mutationCount.value > 0);
};
