import { useCurrentReceiptSubscription, useGetReceipt, useGetReceiptTemplate } from '@/api/receipt';
import { ENV } from '@/utils/env';
import { ApolloError } from '@apollo/client/core';
import * as Sentry from '@sentry/vue';
import { GraphQLError } from 'graphql';
import { decompressFromBase64 } from 'lz-string';
import { ComputedRef, Ref, computed, reactive, unref, watch } from 'vue';
import { LocationQueryValue } from 'vue-router';
import { useAuthManager } from './auth';
import { useLocalStorageManager } from './localstorage';

export function useSubscribeToReceipt({
  transactionRefs,
  mode,
  enabled,
}: { transactionRefs?: Ref<string[] | undefined>; mode?: Ref<string>; enabled?: Ref<boolean> } = {}) {
  const currentReceiptSubscription = useCurrentReceiptSubscription(
    computed(() =>
      reactive({
        transactionRefs: transactionRefs?.value,
      })
    ),
    {
      enabled: computed(() => mode?.value !== 'share' && (!enabled || enabled.value)),
    }
  );
  const { isErrored, error, data } = currentReceiptSubscription;

  watch([isErrored, error], ([isErrored, error]) => {
    if (!error || !isErrored) return;
    if (error instanceof ApolloError) {
      const { graphQLErrors, networkError } = error;
      if (ENV.DEV) console.error('Error: onError Current Receipt Subscription', graphQLErrors, networkError);
      Sentry.captureException(error);
    }
    if (Array.isArray(error) && error[0] instanceof GraphQLError) {
      Sentry.captureException(error);
      error.forEach((e) => {
        if (ENV.DEV) console.error('Error: onError Current Receipt Subscription', e);
      });
    }
  });

  const latestReceiptSubscription = computed(() => data.value?.currentReceipt);

  return { ...currentReceiptSubscription, latestReceiptSubscription };
}

export function useGetCurrentReceipt({
  isAuthenticated,
  isAnonymous,
  transactionRef,
  receiptMode,
  enabled,
}: {
  isAuthenticated: ComputedRef<boolean>;
  isAnonymous: ComputedRef<boolean>;
  transactionRef?: Readonly<Ref<LocationQueryValue>>;
  receiptMode?: Readonly<Ref<LocationQueryValue>>;
  enabled: Readonly<Ref<boolean>>;
}) {
  const enableGetReceipt = computed(() => (unref(isAuthenticated) || unref(isAnonymous)) && enabled.value);

  const getReceiptQuery = useGetReceipt(
    computed(() =>
      reactive({
        where: {
          ref: transactionRef,
        },
        mode: receiptMode,
      })
    ),
    { enabled: enableGetReceipt },
    // for invalidating the query when authenticated status changes
    computed(() => reactive({ isAuthenticated, isAnonymous }))
  );

  return getReceiptQuery;
}

export function useGetReceiptTemplateForCompany({ companyName, enabled }: { companyName: Ref<string>; enabled?: Ref<boolean> }) {
  const LocalStorageManager = useLocalStorageManager();
  const { isAuthenticated, isAnonymous } = useAuthManager();

  const enabledGetReceipt = computed(() => !!companyName.value && (!enabled || enabled.value) && (unref(isAuthenticated) || unref(isAnonymous)));
  const getReceiptTemplate = useGetReceiptTemplate(
    computed(() =>
      reactive({
        where: { companyRef: companyName },
      })
    ),
    {
      enabled: enabledGetReceipt,
      initialData: companyName.value
        ? {
            getReceiptTemplate: {
              template: LocalStorageManager.getReceiptTemplateCache(companyName.value),
            },
          }
        : undefined,
    }
  );
  const { isSuccess, data, isLoading } = getReceiptTemplate;

  const template = computed(() => {
    const template = data.value?.getReceiptTemplate?.template;
    return template ? decompressFromBase64(template) : undefined;
  });

  watch([isSuccess, template], ([isSuccess, template]) => {
    if (!isSuccess || !template) return;
    if (template) {
      if (companyName.value) LocalStorageManager.setReceiptTemplateCache(companyName.value, template);
    } else {
      Sentry.captureMessage('Error: Template is missing for this receipt');
      if (ENV.DEV) console.error('Error: Template is missing for this receipt', data);
    }
  });

  // when API is enabled and no template is found for this company
  const noTemplateFound = computed(() => enabledGetReceipt.value && !isLoading.value && !template.value);

  watch([noTemplateFound], ([noTemplateFound]) => {
    if (!noTemplateFound) return;
    Sentry.captureMessage('Error: No receipt template found for this company: ' + companyName.value);
    if (ENV.DEV) console.error('Error: No receipt template found for this company', companyName.value);
  });

  return { ...getReceiptTemplate, template, noTemplateFound };
}

export function getCompanyLogoURL(logos: { [key: string]: string }) {
  return logos?.svg || logos?.png || logos?.jpg || logos?.jpeg;
}
